span,
.kep-login-facebook,
.metro,
.kep-login-facebook.metro {
  all: unset;
}

.App {
  height: 100vh;
}
.App__mainpage {
  margin: 40px auto 20px;
  text-align: center;
  width: 70.83%;
}
@media (max-width: 1400px) {
  .App__mainpage {
    width: 86.67%;
  }
}
.App__button-auth {
  height: inherit;
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 10px;
}
@media (max-width: 600px) {
  .App__button-auth {
    flex-direction: column;
    gap: 10px;
  }
}
.App__button-auth > * {
  align-self: center;
  padding: 15px 20px;
  height: 10%;
  width: 40%;
  cursor: pointer;
  border-radius: 100px;
  border: 1px solid;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  background-color: #fff;
  color: #616467;
  box-shadow: inset 0 0 0 2px #616467;
  transition: all 0.5s ease-in-out;
}
.App__button-auth > *:hover {
  background-color: #616467;
  border-color: #000000;
  color: #ffffff;
}
@media (max-width: 800px) {
  .App__button-auth > * {
    width: 90%;
  }
}

.header-image {
  max-width: 600px;
  max-height: 200px;
}
@media (max-width: 700px) {
  .header-image {
    width: 100%;
    height: auto;
  }
}

.details-text {
  height: 100px;
  padding: 20px;
  font-weight: 400;
  font-size: 32px;
  line-height: 38px;
  text-align: center;
}

.button-auth {
  padding: 15px 20px;
  cursor: pointer;
  border-radius: 100px;
  border: 1px solid;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  color: #616467;
  box-shadow: inset 0 0 0 2px #616467;
}
.button-auth:hover {
  background-color: #616467;
  border-color: #000000;
  color: #ffffff;
}

.metro {
  height: -moz-min-content;
  height: min-content;
  align-self: center;
}

span.style {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
}/*# sourceMappingURL=App.css.map */